<template>
		<v-dialog
      v-model="dialog"
			fullscreen
		>

			<template v-slot:activator="{on, attrs}">
				<v-btn
					icon
					v-bind="attrs"
					v-on="on"
				>
					<v-icon>mdi-checkbox-multiple-blank</v-icon>
				</v-btn>
			</template>

			<template v-slot:default>
				<v-card tile>
          <v-btn
						fab
						fixed
						right
						class="close-button"
						@click="dialog = false"
					>
            <v-icon>mdi-close</v-icon>
          </v-btn>
					<v-item-group
						v-model="$store.state.bill.activeList"
						mandatory
					>
						<v-container>
							<v-row justify="space-around">
								<v-item
									v-for="(list, index) of lists"
									:key="index"
									v-slot:default="{ active, toggle }"
									class="ma-3"
								>
									<v-card
										:color="active ? 'secondary' : 'primary'"
										class="d-flex align-center text-center"
										dark
										height="130px"
										width="130px"
										@click="pickList(toggle, index)"
									>
										<v-card-text>
											<p class="card-text">
												{{ listTitle(list, index) }}
											</p>
										</v-card-text>
									</v-card>
								</v-item>
							</v-row>
						</v-container>
					</v-item-group>
				</v-card>
			</template>
		</v-dialog>
</template>

<script>
	export default {
		data() {
			return {
				dialog: false
			}
		},
		methods: {
			hide() {
				this.$store.commit('bill/hideListPicker')
			},
			listTitle(list, index) {
				return list.supplier || `Lista ${index+1}`
			},
			pickList(toggleFunction, index) {
				this.dialog = false
				this.$store.commit('bill/updateActiveList', index)
				toggleFunction()
			}
		},
		computed: {
			lists() {
				return this.$store.state.bill.lists
			}
		}
	}
</script>

<style scoped>
	.close-button {
		margin-top: 20px;
		margin-right: 20px
	}
	.card-text {
		font-size: large;
		color: white
	}
</style>