<template>
	<v-container fluid>
		<shopping-list-header />
		<itemList />
		<v-footer
			app
			height="54"
			tile
			color="primary"
			dark
			elevation="2"
		>
			<v-col
				cols="12"
				class="text-center font-weight-bold"
			>
				Total: {{ currencyFormat(total) }}
				<item-creator />
			</v-col>
		</v-footer>
	</v-container>
</template>

<script>
	import shoppingListHeader from '@/components/shopping-list-header'
	import itemList from '@/components/item-list'
	import itemCreator from '@/components/item-creator'
	import helpers from '@/mixins/helpers'
	import { fixNumberRepresentationImprecision } from '@/lib/number-representation'
	export default {
		components: { shoppingListHeader, itemList, itemCreator },
		mixins: [ helpers ],
		computed: {
			total() {
				const { lists, activeList } = this.$store.state.bill
				const list = lists[activeList].items
				const result = list.reduce( (sum, { subtotal }) => sum+subtotal, 0 )
				return fixNumberRepresentationImprecision(result, 2)
			}
		}
	}
</script>