<template>
	<v-app-bar
		app
		tile
		color="primary"
		dark
		elevation="2"
	>
		<v-toolbar-title> {{ formatSupplier }} </v-toolbar-title>
		<v-edit-dialog
			large
			save-text="Editar"
			cancel-text="Cancelar"
			@open="openUpdateSupplier"
			@save="updateSupplier"
		>
			<template v-slot:default>
				<v-btn
					icon
					x-small
					class="supplier-update-icon"
				>
					<v-icon>mdi-pencil</v-icon>
				</v-btn>
			</template>
			<template v-slot:input>
				<v-text-field
					v-model="newSupplierValue"
					placeholder="Proveedor"
					clearable
				/>
			</template>
		</v-edit-dialog>
		<v-spacer></v-spacer>
		<v-menu
			offset-y
			transition="slide-x-reverse-transition"
		>
			<template v-slot:activator="{on}">
				<v-btn
					icon
					v-on="on"
				>
					<v-icon>mdi-dots-vertical</v-icon>
				</v-btn>
			</template>
			<template v-slot:default>
				<v-list
					color="primary"
					dark
				>
					<v-list-item-group>
						<v-list-item
							v-for="(option, index) in menuOptions"
							:key="index"
							@click="option.method"
						>
							<v-list-item-icon>
								<v-icon> {{ option.icon }} </v-icon>
							</v-list-item-icon>
							<v-list-item-content>
								<v-list-item-title> {{ option.text }} </v-list-item-title>
							</v-list-item-content>
						</v-list-item>
					</v-list-item-group>
				</v-list>
			</template>
		</v-menu>
		<listPicker />
	</v-app-bar>
</template>

<script>
	import listPicker from '@/components/list-picker'
	export default {
		components: { listPicker },
		data() {
			return {
				newSupplierValue: null,
				menuOptions: [
					{ icon: 'mdi-plus', text: 'Nueva lista', method: this.createList },
					{ icon: 'mdi-close', text: 'Cerrar lista', method: this.closeList }
				]
			}
		},
		methods: {
			createList() {
				this.$store.commit('bill/addList')
			},
			closeList() {
				this.$store.commit('bill/closeList')
			},
			showListPicker() {
				this.$store.commit('bill/showListPicker')
			},
			openUpdateSupplier() {
				this.newSupplierValue = this.supplier
			},
			updateSupplier() {
				this.$store.commit('bill/updateSupplier', this.newSupplierValue)
				this.newSupplierValue = null
			}
		},
		computed: {
			supplier() {
				const { lists, activeList } = this.$store.state.bill
				return lists[activeList].supplier
			},
			formatSupplier() {
				const { lists, activeList } = this.$store.state.bill
				return lists[activeList].supplier || `Proveedor de lista ${activeList+1}`
			}
		}
	}
</script>

<style scoped>
	.supplier-update-icon {
		margin-left: 6px
	}
</style>