<template>

	<v-data-table
		:headers="headers"
		:items="currentList"
		hide-default-footer
		mobile-breakpoint="0"
		disable-pagination
	>

		<template v-slot:no-data class="fill-height">
			No hay productos en la lista
		</template>

		<template v-slot:item.name="{ item }">
			<v-edit-dialog
				large
				save-text="Editar"
				cancel-text="Cancelar"
				@open="openUpdate(item, 'name')"
				@save="saveUpdate(item, 'name')"
			>
				{{ item.name }}
				<template v-slot:input>
					<v-text-field
						prepend-icon="mdi-cube-outline"
						v-model="updatedValue"
						clearable
					/>
				</template>
			</v-edit-dialog>
		</template>

		<template v-slot:item.price="{ item }">
			<v-edit-dialog
				large
				save-text="Editar"
				cancel-text="Cancelar"
				@open="openUpdate(item, 'price')"
				@save="saveUpdate(item, 'price')"
			>
				{{ currencyFormat(item.price) }}
				<template v-slot:input>
					<v-text-field
						prepend-icon="mdi-cash"
						v-model="updatedValue"
						clearable
						type="number"
						min="0"
						@change="disallowNegatives"
					>
						{{ item.price }}
					</v-text-field>
				</template>
			</v-edit-dialog>
		</template>

		<template v-slot:item.quantity="{ item }">
			<v-edit-dialog
				large
				save-text="Editar"
				cancel-text="Cancelar"
				@open="openUpdate(item, 'quantity')"
				@save="saveUpdate(item, 'quantity')"
			>
				{{ item.quantity }}
				<template v-slot:input>
					<v-text-field
						prepend-icon="mdi-multiplication"
						v-model="updatedValue"
						clearable
						type="number"
						min="0"
						@change="disallowNegatives"
					>
						{{ item.quantity }}
					</v-text-field>
				</template>
			</v-edit-dialog>
		</template>

		<template v-slot:item.subtotal="{ item }">
			{{ currencyFormat(item.subtotal) }}
		</template>

		<template v-slot:item.actions="{ item }">
			<v-icon
				small
				@click="$store.commit('bill/deleteItem', item)"
			>
				mdi-delete
			</v-icon>
		</template>

	</v-data-table>

</template>

<script>
	import helpers from '@/mixins/helpers'
	export default {
		mixins: [ helpers ],
		data () {
			return {
				headers: [
					{ text: 'Producto', value: 'name' },
					{ text: 'Precio',   value: 'price' },
					{ text: 'Cantidad', value: 'quantity' },
					{ text: 'Subtotal', value: 'subtotal' },
					{ text: '',         value: 'actions',  sortable: false }
				],
				updatedValue: null
			}
		},
		methods: {
			openUpdate(item, property) {
				this.updatedValue = item[property]
			},
			disallowNegatives(value) {
				if (value<0) {
					this.updatedValue = 0
				}
			},
			saveUpdate(item, property) {
				this.$store.commit('bill/updateItem', {
					item,
					property,
					value: this.updatedValue
				})
			}
		},
		computed: {
			currentList() {
				const { lists, activeList } = this.$store.state.bill
				return lists[activeList].items
			}
		}
	}
</script>