<template>
	<v-dialog
		v-model="show"
		@click:outside="hide"
	>
		<template v-slot:activator="{ on }">
			<v-btn
				fab
				absolute
				top
				right
				color="secondary white--text"
				v-on="on"
			>
				<v-icon>mdi-plus</v-icon>
			</v-btn>
		</template>

		<v-card>
			<v-card-title>
				<span class="headline"> Agregar producto </span>
			</v-card-title>
			<v-card-text>
				<v-container>
					<v-row>
						<v-col cols="12" sm="4">
							<v-text-field
								v-model="newItem.name"
								label="Producto"
								prepend-icon="mdi-cube-outline"
								autofocus
							/>
						</v-col>
						<v-col cols="6" sm="4">
							<v-text-field
								v-model="newItem.price"
								label="Precio"
								type="number"
								prepend-icon="mdi-cash"
								min="0"
								@change="disallowNegatives($event, 'price')"
							/>
						</v-col>
						<v-col cols="6" sm="4">
							<v-text-field
								v-model="newItem.quantity"
								label="Cantidad"
								type="number"
								prepend-icon="mdi-multiplication"
								min="0"
								@change="disallowNegatives($event, 'quantity')"
							/>
						</v-col>
					</v-row>
				</v-container>
			</v-card-text>
			<v-card-actions>
				<v-spacer></v-spacer>
				<v-btn color="primary" text @click="hide">Cancelar</v-btn>
				<v-btn color="primary" text @click="addItem">Agregar</v-btn>
			</v-card-actions>
		</v-card>
	</v-dialog>
</template>

<script>
	import { fixNumberRepresentationImprecision } from '@/lib/number-representation.js'
	export default {
		data() {
			return {
				show: false,
				newItem: {
					name: '',
					price: '',
					quantity: ''
				}
			}
		},
		methods: {
			disallowNegatives(value, field) {
				if (value<0) {
					this.newItem[field] = 0
				}
			},
			hide() {
				this.show = false
				this.newItem.name = this.newItem.price = this.newItem.quantity = null
			},
			addItem() {
				const { name, price, quantity } = this.newItem
				this.$store.commit('bill/addItem', {
					name,
					price,
					quantity,
					subtotal: fixNumberRepresentationImprecision(price*quantity, 2)
				})
				this.show = false
				this.newItem.name = this.newItem.price = this.newItem.quantity = null
			}
		}
	}
</script>