import Vue from 'vue'
import Vuex from 'vuex'
import createPersistedState from 'vuex-persistedstate'
import { fixNumberRepresentationImprecision } from '@/lib/number-representation'

Vue.use(Vuex)

const billModule = {
	namespaced: true,
	state: {
		lists: [{
			supplier: null,
			items: []
		}],
		activeList: 0
	},
	mutations: {
		addItem: ({ lists, activeList }, item) => lists[activeList].items.push(item),
		updateItem: (state, {item, property, value}) => {
			item[property] = value
			if (property==='price'||property==='quantity') {
				item.subtotal = fixNumberRepresentationImprecision(item.price*item.quantity, 2)
			}
		},
		deleteItem: ({ lists, activeList }, item) => {
			const list = lists[activeList].items
			const itemIndex = list.indexOf(item)
			list.splice(itemIndex, 1)
		},
		addList: (state) => {
			state.lists.push({
				supplier: null,
				items: []
			})
			state.activeList = state.lists.length-1
		},
		closeList: (state) => {
			const { lists, activeList } = state
			const toBeRemoved = activeList
			if (lists.length===1) {
				lists.push({
					supplier: null,
					items: []
				})
			}
			if (activeList===lists.length-1) {
				state.activeList--
			}
			lists.splice(toBeRemoved, 1)
		},
		updateActiveList: (state, newActiveList) => {
			state.activeList = newActiveList
		},
		updateSupplier: (state, newSupplierValue) => {
			state.lists[state.activeList].supplier = newSupplierValue
		}
	}
}

export default new Vuex.Store({
	plugins: [createPersistedState()],
	modules: {
		bill: billModule
	}
})
